:root {
  --primary: #212428;
  --light-gray: #878e99;
  --secondary: #ffffff;
  --active: #ff014f;
}
body::-webkit-scrollbar {
  width: 8px;
 
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: var(--active);
  outline: 1px solid slategrey;
  border-radius: 10px;
}
a:hover{
  color: var(--active) !important;
  transition: all 0.5s ease;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--primary);
}

.top-header {
  width: 100%;
  /* background-color: var(--active); */
  height: 50vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  /* background-image: url(./assets/images/top-headerbg.jpg); */
}

.top-sub-header {
  background-color: rgba(0,0,0,0.6);
  margin-top: -100px;
  border-radius: 10px;
  box-shadow: 2px 2px 2px var(--light-gray);
  min-height: 25vh;
}

.mlr-40 {
  margin: 0px 40px 0px 40px !important;
  padding: 20px;
}

.user-logo-box-wrap {
  position: relative;
}

.user-logo-box {
  width: 160px;
  height: 160px;
  border-radius: 100%;
  max-width: 100%;
  position: absolute;
  top: -30%;
  z-index: 999999;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--secondary);
  box-shadow: 1px 1px 1px 1px var(--active);
}

.user-logo-box img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  margin:0;
  padding: 0;
}

.top-sub-header__title {
  font-weight: 800;
  text-transform: uppercase;
}

.top-sub-header__subtitle {
  font-weight: 600;
  text-transform: uppercase;
  color: var(--light-gray);
  text-transform: capitalize;
}

.top-sub-header__socialList {
  display: flex;
  list-style: none;
  justify-content: start;
  margin: 0;
  padding: 0;
}

.top-sub-header__socialList li {
  margin-right: 10px;
}

