.common-title {
  color: var(--light-gray);
  font-size: 20px !important;
 
}

.common-title-name {
  color: var(--secondary);
  font-size: 16px !important;
  text-decoration: none;
}


/* Tabs */

.tab-container{
  margin-top: 25px;
}
.p-0{
  /* margin: 0 !important; */
  padding: 0 !important;
}
