  width: 100% !important;
}
.mt-20{
  margin-top:20px;
}
.academia-lists{
  margin: 0;
  padding: 0;
}
.academia-lists li{
 
  position: relative;
  list-style: none;
  padding-left: 30px;
  margin-bottom: 10px;
}
.academia-lists li::after{
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--active);
  content: '>';
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--secondary);
  font-size: 12px;
  box-shadow: 2px 2px 2px var(--primary);
}
.project-bullet{
  font-weight: 800;
  color: #212428;
}

.card-bg-repeat{
  position: relative;
  box-sizing: border-box;
  /* background-image: linear-gradient(to right, #14e5eb, #00c1f9, #0096f5, #6f61cf, #9c0686); */
}
.card-bg-repeat:hover{
  /* background-image: linear-gradient(to right, #9c0686, #6f61cf, #0096f5, #00c1f9, #14e5eb); */
}
.card-activeted::after{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 2%;
  height: 100%;
  background-color: var(--active);
  /* z-index: 999; */
}
.text-active{
  color: var(--active);
  font-weight: 800;
}

.download-resume{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}
.resume-btn{
  border-radius: 20px;
  padding: 3px 20px;
  background-color: var(--active);
  font-size: 16px;
  box-shadow: 2px 2px #212428;
  color: #ffffff;
  text-decoration: none;
}
.resume-btn:hover{
  color: var(--primary) !important;
  transition: all 0.5s ease;
}
