.card-bg::before{
   content: '';
   width: 118px;
   height: 103px;
   background-image: url(../src/assets/images/work.png);
   background-repeat: no-repeat;
   background-size: contain;
   position: absolute;
   right: 0;
   bottom: 0;
   box-shadow: -2px -2px -2px var(--active);
   z-index: 0;
}

